        function getBoundary(c){       
            var bdary = new BMap.Boundary();
            bdary.get(c, function(rs){       //获取行政区域
                
                bmap.clearOverlays();        //清除地图覆盖物       
                var count = rs.boundaries.length; //行政区域的点有多少个
                if (count === 0) {
                    alert('未能获取当前输入行政区域');
                    return ;
                }
                var pointArray = [];
                for (var i = 0; i < count; i++) {
                    var ply = new BMap.Polygon(rs.boundaries[i], {strokeWeight: 0.1, fillOpacity:0.8, fillColor:'green'}); //建立多边形覆盖物
                    bmap.addOverlay(ply);  //添加覆盖物
