    .main-right{
        width 35%
        float left
        height 83vh
        overflow scroll
        //background red
        .main-right-inner{
            width 100%
            height 80vw
        }
    }
  }
  .main{
    width 100%
    height 90vh
    margin-top 5vh
    .analysis{
        width 75%
        
        height 75%
        margin auto

    }
    .result{
        width 60%
        min-height 20vh
        font-size 20px
        font-weight bold
        line-height 1.7

        text-indent 40px
        //display inline
        margin auto
        //display flex
        //background yellow
        .r-title{
            text-align center
            width 50%
            margin auto
        }
        .iw{
            display flex
            margin auto
            margin-top 20px
            margin-bottom 20px
        }
        .for-img{
            width 20vw
            height 15vw
            margin auto
            img{
                width 100%
            }
        }
        .resultGraph{
            width 35vw
            height 35vw
            float right
            //background green
        }
        .con{
            font-size 15px
            text-indent 30px
            width 50%
            height 35vw
            float left
            overflow hidden
        }
        .content-bottom{
            font-size 15px
            text-indent 30px
            line-height 2
            letter-spacing 4px
        }
    }
        
  }
}
</style>


