<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  created(){
    //this.get_zhengzhou()
  },
  methods:{
        get_zhengzhou(){
        var that = this
        axios.get('static/api/zhengzhou_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.$store.state.zhengzhou_json.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    //color: 'red'
                                }
                            }
                            
                        }
                    )
                }
            
            }
        
        
        )
    
    },
  },
}
</script>

<style>
.BMap_cpyCtrl {
   display: none;
 }
.anchorBL {
   display: none;
}
</style>



