            }
        }
    }
    .result{
        width 100%
        //height 200px
        .r-title{
            //background yellow
            width 50%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            margin-left 15px
            line-height 50px
        }
        .bobao{
            width 100%
            height 40px
            line-height 40px
            text-align center
            font-size 13px
            font-weight bold
            //text-indent 10px
        }
        .result-content{
            border-bottom 1px solid #eae8e8
            width 90%
            line-height 30px
            
            background white
            display flex
            height 30px
            //background red
            margin auto
            //margin-bottom 3px
            img {
                width 100%
            }
            .address{
                float left
                height 100%
                white-space nowrap
                overflow hidden
                text-overflow ellipsis
                padding-right 20px
                width 50%
                border-right 1px solid #eae8e8
                text-indent 10px
            }
            .origin{
                float left
                height 100%
                text-align center
                width 50%
            }
        }
        
    }
    .for-charts{
        width 100%
        height 300px
        margin-bottom 100px
        .r-title{
            //background yellow
            width 50%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            margin-left 15px
            line-height 50px
        }
        .myCharts{
            width 100%
            height 90%
            //background red
            margin auto
        }
    }
  }

.anchorBL{
    display:none;
}
}
.anchorBL{
    display:none;
} 
</style>


