                    that.shangqiu_json.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    //color: 'red'
                                }
                            }
                            
                        }
                    )
                }
            that.initTraceCharts(that.shangqiu_json,that.shangqiu_center,that.nomal_zoom)
            }
        )
    },
    get_xinyang_json(){
        var that = this
        // 信阳市
        axios.get('static/trace/xinyang_patient_simple.json').then(function(res){
