    // 郑州市
    get_luoyang(){
        var that = this
         axios.get('static/trace/luoyang_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.luoyang_json.push(
                        {
