        axios.get('static/traceData/luoyang.json').then(function(res){
            console.log(res.data)
            that.luoyang_td = res.data
            that.td = res.data
        })
    
    },
    
    // 轨迹图
    initTraceCharts(data,center,zoom){
        let myChart = echarts.init(this.$refs.map);
        //窗口变动监听
        window.addEventListener('resize', function () {
            myChart.resize()
        })
        /*
        function renderItem(params, api) {
            var coords = that.zhengzhou_json;
            var points = [];
            for (var i = 0; i < coords.length; i++) {
                points.push(api.coord(coords[i]));
            }
            var color = api.visual('color');

            return {
                type: 'polygon',
                shape: {
                    points: echarts.graphic.clipPointsByRect(points, {
                        x: params.coordSys.x,
                        y: params.coordSys.y,
                        smooth:0,
                        width: params.coordSys.width,
                        height: params.coordSys.height
                    })
                },
                style: api.style({
                    fill: 'green',
                    lineWidth:2,
                    stroke: 'rgb(43, 123, 56)'
                })
            };
        }*/
        var d = data
        //console.log(geojson)
        myChart.setOption({
            bmap: {
                center: center,
                //center: [113.662006,34.749485],
                zoom: zoom,
                roam: true,
                mapStyle: {
                  'styleJson': [
                    {
                      'featureType': 'water',
                      'elementType': 'all',
                      'stylers': {
                        //'color': '#2d78f4'
                       // 'color': '#031628'
                        'color': '#1a1a1a'
                      }
                    },
                    {
                      'featureType': 'land',
                      'elementType': 'geometry',
                      'stylers': {
                       //'color': '#000102'
                       'color': '#292929'
                       
                      }
                    },
                    {
                      'featureType': 'highway',
                      'elementType': 'all',
                      'stylers': {
                        'visibility': 'off'
                      }
                    },
                    {
                      'featureType': 'arterial',
                      'elementType': 'geometry.fill',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'arterial',
                      'elementType': 'geometry.stroke',
                      'stylers': {
                        'color': '#0b3d51'
                      }
                    },
                    {
                      'featureType': 'local',
                      'elementType': 'geometry',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'railway',
                      'elementType': 'geometry.fill',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'railway',
                      'elementType': 'geometry.stroke',
                      'stylers': {
                        'color': '#08304b'
                      }
                    },
                    {
                      'featureType': 'subway',
                      'elementType': 'geometry',
                      'stylers': {
                        'lightness': -70
                      }
                    },
                    {
                      'featureType': 'building',
                      'elementType': 'geometry.fill',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'all',
                      'elementType': 'labels.text.fill',
                      'stylers': {
                        'color': '#857f7f'
                      }
                    },
                    {
                      'featureType': 'all',
                      'elementType': 'labels.text.stroke',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'building',
                      'elementType': 'geometry',
                      'stylers': {
                        'color': '#022338'
                      }
                    },
                    {
                      'featureType': 'green',
                      'elementType': 'geometry',
                      'stylers': {
                        'color': '#062032'
                      }
                    },
                    {
                      'featureType': 'boundary',
                      'elementType': 'all',
                      'stylers': {
                        'color': '#465b6c'
                      }
                    },
                    {
                      'featureType': 'manmade',
                      'elementType': 'all',
                      'stylers': {
                        'color': '#022338'
                      }
                    },
                    {
                      'featureType': 'label',
                      'elementType': 'all',
                      'stylers': {
                        'visibility': 'off'
                      }
                    }
                  ]
                }
            },
            series: [{
                type: 'lines',
                coordinateSystem: 'bmap',
                polyline: true,
                data: d,
                silent: true,
                lineStyle: {
                    // color: '#c23531',
                    // color: 'rgb(200, 35, 45)',
                    opacity: 0.4,
                    width: 1
                },
                progressiveThreshold: 500,
                progressive: 200
            }, {
                type: 'lines',
                coordinateSystem: 'bmap',
                polyline: true,
                //data: busLines,
                data: d,
                lineStyle: {
                    width: 0
                },
                effect: {
                    constantSpeed: 20,
                    show: true,
                    trailLength: 0.1,
                    symbolSize: 1.5
                },
                zlevel: 1
            }/*{
                type: 'custom',
                coordinateSystem: 'bmap',
                renderItem: renderItem,
                itemStyle: {
                    opacity: 0.5
                },
                animation: true,
                silent: true,
                data: [0],
                z: -10
            }*/]

            
        })
        
    }
  }
};
</script>

<style lang="stylus" scoped>
.outer {
  background: #fff;
  height: 100vh;
  //display: flex;
  //flex-direction: column;
  .content {
    display: flex;
    //flex-direction: row;
    //flex: 1;
  }
  .t-menu{
    width 100%
    height 10vh
    background-color rgb(84, 92, 100)
    .menu-top{
        color white
        height 10vh
        line-height 9.6vh
        width 67%
        margin-left 8%
        //background red
        //display flex
        .top-item{
            width 25%
            height 100%
            text-align center
            float left
        }
        .top-item:hover{
            //background-color rgb(67, 74, 80)
            background red
        }
    }
    .menu-bottom{
        width 16.75%
        height 0.4vh
        margin-left 8%
        margin-top -0.4vh
        transition transform 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955)
        background rgb(255, 208, 75)
    }
  }
  .el-menu-demo {
    padding: 0 110px;
    li {
      width: 20%;
      text-align: center;
    }
  }
  .el-row {
    .el-col {
      height: 100%;
      ul {
        height: 100%;
      }
    }
  }
  #container {
    width: 65%;
    height: 90vh;
    background darkgray
  }
  .right-content{
    width: 35%
    height: 90vh
    background: #eae8e8
    overflow scroll
    .r-top{
        box-sizing boder-box
        padding-left 10px
        .r-title{
            //background yellow
            width 100%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            line-height 50px
        }
        .r-form{
            width 90%
            margin auto
            //background green
            height 40px
            line-height 40px
            display flex
            .form-left{
                width 40%
                font-size 10px
                
            }
            .form-right{
                height 30px
                margin-top 5px
                //text-align center
                letter-spacing 4px
                width 60%
            }
        }
    }
    .result{
        width 100%
        //height 200px
        .r-title{
            //background yellow
            width 90%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            //margin-left 20px
            display flex
            margin auto
            line-height 50px
            .r-t-l{
                width 50%
                
            }
            .r-t-r{
                width 50%
                text-align center
            }
        }
        .result-content{
            //border 1px solid darkgreen
            width 90%
            //height 70%
            margin auto
            font-weight bold
            line-height 1.5
            //text-indent 10px
            margin-bottom 10px
            
        }
        .result-content > p{
                text-indent 10px
            }
        
    }
    .for-charts{
        width 100%
        height 300px
        margin-bottom 100px
        .r-title{
            //background yellow
            width 50%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            margin-left 15px
            line-height 50px
        }
        .myCharts{
            width 100%
            height 90%
            //background red
            margin auto
        }
    }
  }
}


</style>


